import React from "react";
import classes from './Friends.module.scss'
import {FriendsType} from "../../redux/state";


type PropsType = {
    friends: Array<FriendsType>
}

const Friends = (props:PropsType) => {
    let friends_bar = props.friends.map(name => (
        <div className={classes.profile_body}>
            <div className={classes.profile_photo}>
                <img src={name.img} alt=""/></div>
            <div className={classes.profile_name}>{name.name}</div>
        </div>)
    )


    return (
        <div className={classes.body}>
            {friends_bar}
            {friends_bar}
        </div>
    );
};

export default Friends;
